html, body {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;

}
.main {
        text-align: center;
        background-color: #262626;
        width: 100%;
        padding-bottom: 50px;        
        height: 100vh;

}
.vs1{
        width: 100%;
}


.titulos{
        color: white;
        margin: 25px;
        font-size: 35px;
        font-family:  Arial, Helvetica, sans-serif;

}
.comparacionequipos{

        display:flex;
        background-color: #262626;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding-top: 30px;
        padding-bottom: 45px;
        width: auto;
        top: 0px;

}
.selectors{
        margin-top: 0px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-right: 10%;
        padding-left: 10%;
        background-color: aquamarine;
}

@media (max-width:850px) {
        .form-control{
                margin-top: 8px;
                top:80px;
        }
        .comparacionequipos{
                margin: 0px;
                padding: 0px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
        }
        .selectors{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
        }
}