/*Navbar*/
*{
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        list-style: none;
}
nav{
        background-color: #e40505;
        color: #fff;
        
        height: 60px;
}
.menu {
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto;
        width: auto;
}


.menu li {
        height:60px;
        width: 150px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
}

li:hover{
        color: rgb(255, 255, 255);
        background-color: #262626;
}

.menu a {
        text-decoration: none;
        color: #fff;
        font-family:  Arial, Helvetica, sans-serif;
        font-size: 15px;
}
a::before{
        color: #fff;
}
.enlace{
        position: absolute;
        padding: 100px 50px;
        padding-left: 500px;
        background-color: #000000;
}

.checkbtn{
        font-size: 30px;
        color: #ffffff;
        float: right;
        line-height: 10px;
        cursor: pointer;
        display: none;
        text-decoration: none;
}
#check{
        display: none;
}
.res{
        width: 1000px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-top: 25px;
}

@media (max-width:850px) {

.enfrentamientos{
        width: 100%;
}

        .menu li {
                width: 100%;
        }
        img{
                float: right;
                justify-self: center; /* Centra horizontalmente la imagen en el contenedor grid */
                margin-top: 10px;
                margin-right: 20px;
        }
        .esc_local{
                margin: 0px;
        }
        .esc_visita{
                margin: 0px;
                padding: 0px;
        }
        .checkbtn{
                display: block;
                float: right;
                background-color: #e40505;
                padding-left: 80%;
        }
        ul{
                position: fixed;
                width: 100%;
                background-color: rgb(148, 173, 37);
                top: 60px;
                left: -100%;
                text-align: center;
                transition: all 1.5s;
                flex-direction: column;
                padding: 0px;
                margin: 0px;
        
        }
        .menu{
                background-color: #a71414;
                width: 100%;
        }


        .form-control{
                margin: 0px;
        }

        ul li{
                display:block;
                width: 100%;
                justify-content: center;
                align-items: center;
                background-color: #e40505c7;
                
        }



        li a:hover, li a.active{
                background: none;
                color: #ffffff;
        }
        #check:checked ~ ul{
                left: 0;
                background-color: #e40505;
        }
        .active{
                background-color: #262626;
                height: 60px;
                width: 150px;
                text-align: center;
                display: grid;
                justify-items: center;
                align-items: center;
                text-decoration: underline;
                color: white;
        }
        .resultados{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

        }

}
@media (max-width:500px) {
        .res{
                width: 1000px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
        }
        .checkbtn{
                display: block;
                width: 20px;
                padding: 0px;
        }
        img{
                float: right;
                justify-self: center; /* Centra horizontalmente la imagen en el contenedor grid */
                margin-top: 10px;
                margin-right: 20px;
        }

        ul{
                position: fixed;
                background-color: #5e2fb4;
                top: 60px;
                left: -100%;
                text-align: center;
                transition: all 1s;
        
        }

        .active{
                background-color: #262626;
                height: 60px;
                width: 150px;
                text-align: center;
                display: grid;
                justify-items: center;
                align-items: center;
                text-decoration: underline;
