        }
        nav ul li a{
                font-size: 20px;
                color: #ffffff;


        }
        li a:hover, li a.active{
                color: #ffffff;
                padding: 10px;
        }
        #check:checked ~ ul{
                background-color: #0a7063;
                margin-left: 0px;
                padding-left: 0px;
        }
}
.active{
